import React from "react";

import "./OverralProvince.css";

import shield from "../assets/shield.svg";

export default function OverralProvince({ covidData, totalCovid }) {
  return (
    <div className="Overrall-Province">
      <div className="province-number">
        <ul>
          {covidData.map((covid, i) => {
            const total = (
              (covid.positive / totalCovid("positive")) * 100 +
              30
            ).toFixed(0);

            return (
              <li key={i}>
                <span
                  style={{
                    width: `${total}px`,
                    height: `${total}px`,
                    backgroundColor: total > 35 ? "#7fbcbe" : "#f2f6f3",
                    color: total > 35 ? "#fff" : "#7fbcbe",
                  }}
                >
                  <span>{covid.positive}</span>
                </span>
              </li>
            );
          })}
        </ul>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <ul>
          <li>EC</li>

          <li>FS</li>

          <li>GP</li>

          <li>KZN</li>

          <li>LP</li>
          <li>MP</li>

          <li>NW</li>
          <li>NC</li>

          <li>WC</li>
          <li>UN</li>
        </ul>
      </div>
      <div className="covid-statistics">
        <div>
          <figure>
            <img src={shield} alt="shield" />
            <span>{totalCovid("positive")}</span>
          </figure>

          <h4>Total</h4>
        </div>
        <div>
          <h2>Vulnerable Provinces</h2>
          {covidData.map((covid, i) => {
            return (
              covid.positive >= 300 && (
                <ul key={i}>
                  <li>
                    <span>{covid.positive}</span>
                    <span>{covid.province}</span>
                  </li>
                </ul>
              )
            );
          })}
        </div>
      </div>
    </div>
  );
}
