import React from "react";

import { covidDemographics } from "../../data/covid_data";

import "./CoronaGenderBased.css";

export default function CoronaGenderBased() {
  const totalGrandTotal = (female, male, unknown) =>
    covidDemographics.reduce((total, sex) => {
      return total + sex[female] + sex[male] + +sex[unknown];
    }, 0);

  const totalGrandTotalBySex = (female, male, unknown) =>
    covidDemographics.reduce((total, sex) => {
      return total + sex[female];
    }, 0);

  return (
    <div className="Gender-Based">
      <div className="table">
        <ul>
          <li>Age group</li>
          <li>Female</li>
          <li>Male</li>
          <li>Unknown</li>
          <li>Total</li>
        </ul>

        {covidDemographics.map((covid, i) => {
          return (
            <ul key={i}>
              <li>{covid.ageGroup}</li>
              <li>{covid.female}</li>
              <li>{covid.male}</li>
              <li>{covid.unknown}</li>
              <li>
                {covid.female + covid.male + covid.unknown} (
                {(
                  ((covid.female + covid.male + covid.unknown) /
                    totalGrandTotal("female", "male", "unknown")) *
                  100
                ).toFixed(2)}
                %)
              </li>
            </ul>
          );
        })}
        <ul>
          <li>Grand Total</li>
          <li>
            {totalGrandTotalBySex("female")} (
            {(
              (totalGrandTotalBySex("female") /
                totalGrandTotal("female", "male", "unknown")) *
              100
            ).toFixed(2)}
            )
          </li>
          <li>
            {" "}
            {totalGrandTotalBySex("male")} (
            {(
              (totalGrandTotalBySex("male") /
                totalGrandTotal("female", "male", "unknown")) *
              100
            ).toFixed(2)}
            )
          </li>
          <li>
            {" "}
            {totalGrandTotalBySex("unknown")} (
            {(
              (totalGrandTotalBySex("unknown") /
                totalGrandTotal("female", "male", "unknown")) *
              100
            ).toFixed(2)}
            )
          </li>
          <li>{totalGrandTotal("female", "male", "unknown")} (100%)</li>
        </ul>
      </div>
    </div>
  );
}
